<template>
    <div v-if="patient.id">
        <!-- First Section -->
        <div class="section">
            <div class="p-grid">
                <div class="p-col-12 block-container">
                    <div class="block-item">
                        <div style="margin-right: 15px;">
                            <img
                                v-if="patient.sex == 'F'"
                                style="width: 100px;"
                                alt="Vue logo"
                                src="../assets/women.png"
                            />
                            <img
                                v-else
                                style="width: 100px;"
                                alt="Vue logo"
                                src="../assets/men.png"
                            />
                        </div>
                        <div>
                            <div style="margin-bottom: 10px;">
                                <div class="title">
                                    PATIENT
                                </div>
                                <span
                                    >{{ patient.firstName }}
                                    {{ patient.lastName }}</span
                                ><br />
                                <span class="p-id">{{ patient.id }}</span
                                >
                            </div>

                            <span class="p-buttonset">
                                <p-button
                                    label="Barcode"
                                    class="p-button-sm"
                                    @click="showBarcodeV2(patient)"
                                />
                                <p-button
                                    class="p-button-sm"
                                    icon="pi pi-print"
                                    @click="printToZebraPrinter(patient)"
                                />
                            </span>
                        </div>
                    </div>
                    <div class="block-item" v-if="partner.id">
                        <div style="margin-right: 15px;">
                            <div style="margin-bottom: 10px;">
                                <div class="title">
                                    PARTNER
                                </div>
                                <span
                                    >{{ partner.firstName }}
                                    {{ partner.lastName }}</span
                                ><br />
                                <span class="p-id">{{ partner.id }}</span
                                >
                            </div>
                            <span class="p-buttonset">
                                <p-button
                                    label="Barcode"
                                    class="p-button-sm"
                                    @click="showBarcodeV2(partner)"
                                />
                                <p-button
                                    class="p-button-sm"
                                    icon="pi pi-print"
                                    @click="printToZebraPrinter(partner)"
                                />
                            </span>
                        </div>
                        <div>
                            <img
                                v-if="partner.sex == 'F'"
                                style="width: 100px;"
                                alt="Vue logo"
                                src="../assets/women.png"
                            />
                            <img
                                v-else
                                style="width: 100px;"
                                alt="Vue logo"
                                src="../assets/men.png"
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <!-- Second Section -->
        <div class="section" style="margin-top: 20px;">
            <div class="container">
                <!-- Left Box -->
                <div class="left-box">
                    <div v-for="item in patientLabTests"  class="inline-box">
                        <div>
                            <div style="margin-bottom: 10px; font-weight: bold; display: flex; justify-content: center; align-content: center;">
                                <span>
                                    {{ item.name }}
                                </span><br />
                            </div>
                            <div style="display: flex; justify-content: center; align-content: center;">

                                <img :src="getIconUrl(item)" alt="Image" style="width: 50px; height: 50px" />
                            </div>
                            <div>
                                <span class="p-buttonset">
                                    <p-button
                                        label="Barcode"
                                        class="p-button-sm"
                                        @click="showBarcodeV2(patient)"
                                    />
                                    <p-button
                                        class="p-button-sm"
                                        icon="pi pi-print"
                                        @click="printToZebraPrinter(patient)"
                                    />
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
                
                <!-- Right Box -->
                <div class="right-box">    
                    <div v-for="item in partnerLabTests" class="inline-box">
                        <div>
                            <div style="margin-bottom: 10px; font-weight: bold; display: flex; justify-content: center; align-content: center;">
                                <span>
                                    {{ item.name }}
                                </span><br />
                            </div>
                            <div style="display: flex; justify-content: center; align-content: center;">
                                <img :src="getIconUrl(item)" alt="Image" style="width: 50px; height: 50px" />
                            </div>
                            <div>
                                <span class="p-buttonset">
                                    <p-button
                                        label="Barcode"
                                        class="p-button-sm"
                                        @click="showBarcodeV2(patient)"
                                    />
                                    <p-button
                                        class="p-button-sm"
                                        icon="pi pi-print"
                                        @click="printToZebraPrinter(patient)"
                                    />
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>           
        </div>        
    </div>
    <div v-else>
        <h2>No patient selected</h2>
    </div>
</template>
<script>
import { $axios } from "@/service/app-http";
import { onMounted, ref, watch } from "vue";
import { useStore } from "vuex";
import { serialize } from "php-serialize";
import { ZEBRA_PRING_URL, NL_FORM_BASE } from "@/constants";
import { ORIGIN } from "./../config";

export default {
    setup() {
        const printToZebraPrinter = (patient) => {
            let base = `${ZEBRA_PRING_URL}/patient_zpl_print.php`;

            let url =
                `${base}` +
                `?first_name=${patient.firstName}` +
                `&last_name=${patient.lastName}` +
                `&chart=${patient.chart}` +
                `&dob=${patient.year}-${patient.month}-${patient.day}` +
                `&gender=${patient.sex}`;

            window.open(url);
        };
        const store = useStore();
        const logsData = ref([]);

        const patientLabTests = ref([]);
        const partnerLabTests = ref([]);
       
        const loading = ref(false);

        const patient = ref({});
        const partner = ref({});

        const showBarcodeV2 = (patient) => {
            const payload = {
                bar_id: patient.id
            };

            let url = `${NL_FORM_BASE}/jrsPHPclientV2/jrs/generate_reportn1.php?report_name=TESTS_1&report_type=pdf&report_params=${encodeURIComponent(
                serialize(payload)
            )}`;
            window.open(url);
        };

        const getIconUrl = (data) => {
            if (data.icon) {
                return `${ORIGIN}/${data.icon}`;
            } else {
                return "https://via.placeholder.com/300";
            }
        };

        const getPatientLabtests = async (gender) => {

            if(gender == 'M' ||  gender == 'm') gender = 'male';
            if(gender == 'F' ||  gender == 'f') gender = 'female';

            const { data } = await $axios.get(`lab-tests/list/${gender}`, {
                params: {}
            });

            patientLabTests.value = data;
        };

        const getPartnerLabtests = async (gender) => {

            if(gender == 'M' ||  gender == 'm') gender = 'male';
            if(gender == 'F' ||  gender == 'f') gender = 'female';

            const { data } = await $axios.get(`lab-tests/list/${gender}`, {
                params: {}
            });

            partnerLabTests.value = data;
        };

        // const onFilterHandler = () => {
        //     getLogs();
        // };

        onMounted(() => {
            console.log(store.state.artCycle.patient);
            // getLogs();
            if (store.state.artCycle.patient) {
                patient.value = store.state.artCycle.patient;
                
                getPatientLabtests(patient.value.sex);
            }
            if (store.state.artCycle.partner) {
                partner.value = store.state.artCycle.partner;

                getPartnerLabtests(partner.value.sex);
            }
        });

        return {
            logsData,
            loading,
            patient,
            partner,
            store,
            patientLabTests,
            partnerLabTests,
            showBarcodeV2,
            getIconUrl,
            printToZebraPrinter
        };
    }
};
</script>

<style scoped>
.p-card .p-card-content {
    padding: 0;
}

.block-item {
    display: flex;
    border: 1px solid #8cb3d9;
    padding: 10px;
    background-color: white;
}
.block-container {
    display: flex;
    justify-content: space-between;
}
.title {
    margin-bottom: 10px;
    color: darkblue;
    font-weight: bolder;
    background-color: silver;
    text-align: center;
}
.p-id {
    color: brown;
    font-weight: bolder;
}
.container {
    display: flex;
    justify-content: space-between;
}

/* Left box */
.left-box {
    display: flex;
}

/* Right box */
.right-box {
    display: flex;
}

/* Inline boxes */
.inline-box {
    margin: 0 8px; /* Add margin for spacing between boxes */
    display: flex; /* Use flex layout */
    justify-content: center; /* Center content horizontally */
    border: 1px solid #8cb3d9;
}

/* Align left box content */
.left-box .inline-box {
    text-align: left;
}

/* Align right box content */
.right-box .inline-box {
    text-align: right;
}

.inline-box img {
    width: 100px;
    border-radius: 50%; /* Make the image a circle */
}

</style>
